import vCarousel from './components/carousel'
import vPage from './components/page'
export default {
   	data() {
      	return {
            navlist : [
               {
                  title : '关于我们',
                  subTle : 'ABOUT US',
                  link : '/about',
                  img : require('../assets/about.jpg')
               },
               {
                  title : '新闻资讯',
                  subTle : 'NEWS',
                  link : '/news',
                  img : require('../assets/news.jpg')
               },
               {
                  title : '精彩案例',
                  subTle : 'CASES',
                  link : '/cases',
                  img : require('../assets/cases.jpg')
               },
               {
                  title : '服务项目',
                  subTle : 'SERVICE',
                  link : '/service',
                  img : require('../assets/server.jpg')
               },
               {
                  title : '设计团队',
                  subTle : 'TEAM',
                  link : '/team',
                  img : require('../assets/team.jpg')
               },
               {
                  title : '联系我们',
                  subTle : 'CONTACT US',
                  link : '/contact',
                  img : require('../assets/contact.jpg')
               }
            ],
            about_info : '成立与2007年，公司位于紧邻五角场地理位置优越，是一家集广告产品的设计、制作、发布为一体专业化广告公司。公司拥有一整套大型亚克力成型设备、雕刻机、LED灯饰产品等专用设备，以及一批业务精通、经验丰富、技术娴熟、责任心强的设计、制作、安装一体的专业人才队伍。',
            about_info2 : '作为专业化广告公司，某某广告立足于上海广告行业，秉承路有多远，我们的服务就有多远的服务理念，坚持以策略为先导，以品牌服务为核心，为广大客户提供全方位的服务。',
            cases_desc : '秉承路有多远，我们的服务就有多远的服务理念，坚持以策略为先导，以品牌服务为核心，为广大客户提供全方位的服务。',
            caseList : [
               {
                  link : '/',
                  src : require('../assets/caseCar1.jpg'),
                  title : '灯箱广告'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar2.jpg'),
                  title : '灯箱广告设计'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar3.jpg'),
                  title : '其他广告'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar4.jpg'),
                  title : '其他广告设计'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar5.jpg'),
                  title : '装修设计02'
               },
               {
                  link : '/',
                  src : require('../assets/caseCar6.jpg'),
                  title : '灯箱广告设计'
               },
            ],
            liWidth : 180,
            ulWidth : 0,
            ulLeft : 0,
            service_desc: '广告，愿以价廉质优的产品，先进的产品制作工艺，完善的售后服务，诚信的经营理念;，愿与广大客户携手缔造完美灿烂的明天！',
            serviceList : [
               {
                  link : '/',
                  src : require('../assets/servicePic_1.jpg'),
                  title : '包装设计'
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_2.jpg'),
                  title : '包装设计'
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_1.jpg'),
                  title : '包装设计'
               },
               {
                  link : '/',
                  src : require('../assets/servicePic_2.jpg'),
                  title : '包装设计'
               },
            ],
            newsList : [
               {
                  link : '/news/msg_news',
                  title : '广联时代引领泛媒体分账营销方向',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '中国**跨媒介广告营销测算规范开始广',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '中国互联网广告行业 市场规模环比增长',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '为什么有些灯布、压克力上贴膜后会产生',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '多维度效果营销 实现“二主”双赢',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '圆管折叠桁架介绍',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : 'TOM户外赢得安利华北，华东，华南三',
                  date : '2017-12-21'
               },
               {
                  link : '/news/msg_news',
                  title : '2010时报华文广告金像奖邀请函',
                  date : '2017-12-21'
               },
            ],
            pages: [
               {
                  num: 1,
                  status: true
               },
               {
                  num: 2,
                  status: false
               },
            ],
            list: []
      	}
   	},
   	methods: {
         run (){
            this.timer = setInterval(()=>{
               let clientWidth = document.documentElement.clientWidth;
               if (clientWidth >= 640) {clientWidth = 640}
               if (this.ulWidth + this.ulLeft < clientWidth) {    
                  // ul宽度+ul所移动的距离小于屏幕宽度则判断最后一个，则ulleft=0
                  this.ulLeft = 0;
               }else{
                  this.ulLeft-=2;
               }
            },30);
         },
         clear (){
            clearInterval(this.timer); // 移动清楚定时器
         }
   	},
      created() {
         this.ulWidth = (this.liWidth+16) * this.caseList.length; // 动态计算uiWidth
         this.run();
         this.list = this.$store.getters.getCar                   // 从仓库取轮播图
      },
      components: {
         vPage,vCarousel
      }
}



